.header {
  width: 100%;
  font-size: 2rem;
  font-weight: bolder;
  color: rgba(28, 182, 28, 1);
  margin-bottom: 15px;
  background: -webkit-gradient(linear, right top, left top, from(rgba(228, 122, 35,1)), to(rgba(0,128,128,1)));
  background: -webkit-linear-gradient(right, rgba(228, 122, 35,1) 0%, rgba(0,128,128,1) 100%);
  background: -o-linear-gradient(right, rgba(228, 122, 35,1) 0%, rgba(0,128,128,1) 100%);
  background: linear-gradient(to left, rgba(228, 122, 35,1) 0%, rgba(0,128,128,1) 100%);

  /* background: rgb(46, 139, 123); */
  -webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
  box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
}

.title {
  float: left;
  margin-top: 20px;
  font-size: 60px;
  text-shadow: 4px 4px 2px rgba(54,56,49);
  bottom: 0%;
}

.scores {
  float: right;
  font-size: 24px;
  margin-bottom: 10px;
  margin-right: 10px;
}

.instructions {
  float: left;
  top: 0%;
  text-align: center;
  font-size: 24px;
  margin-left: 10px;
  color: rgb(228, 122, 35);
}


